@import "_modal_lifechart.scss";

$sizeResponsive: 1028px !default;

.ramp_lifechart_page {

  &__header {
    display        : flex;
    justify-content: space-between;
    padding        : 10px 0px 10px 0px;
    border-bottom  : 2px solid #bbbbbb;
    margin-bottom  : 20px;

    .options__text {
      display              : grid;
      grid-template-columns: 90% 10%;
      color                : #00b2ed;
      font-size            : 20px;
      padding-top          : 10px;

      .title_option {
        font-weight: bold;
      }

      .icon_img_symp {
        margin-left: 20px;
        cursor     : pointer;
      }

      .icon_img_ass {
        margin-left: 20px;
        cursor     : pointer;
      }

      .icon_img_ov {
        margin-left: 20px;
        cursor     : pointer;
      }
    }

    .button-printer {
      background   : transparent;
      border-radius: 5px;
      border       : 2px solid #aaa;
      color        : #898A8B;
      margin-left  : 0.3em;
      padding      : 5px 10px;
    }
  }

  &__content {
    display       : flex;
    flex-direction: row;
    position: relative;

    @media (max-width: $sizeResponsive) {
      flex-direction: column;
    }

    ul {
      list-style-type: none;
      margin         : 0;

      li {
        padding: 2px;
      }
    }

    a {
      color      : #909090;
      font-size  : 1.2em;
      font-weight: bold;
    }

    .col-left {
      width: 18%;

      a {
        line-height: 2.5em;

        &:hover {
          color: #00b2ed;
        }

        &.active {
          color      : #00b2ed;
          font-weight: bold;
        }
      }

      // border Underline
      li::after {
        content      : " ";
        width        : 85%;
        border-bottom: 2px solid #C2C2C2;
        display      : block;

        @media (max-width: $sizeResponsive) {
          width: 100%;
        }
      }

      li:last-child::after {
        border-bottom: 0px;
      }

      @media (max-width: $sizeResponsive) {
        width     : 100%;
        text-align: center;
      }
    }

    .col-right {
      width       : 22%;
      padding-left: 1em;

      h2 {
        font-size    : 1.5em;
        margin-bottom: 0.7em;
        color        : #707070;
      }

      a {
        line-height: 1.7em;

        &.active {
          color: #44B4B8;
        }
      }

      @media (max-width: $sizeResponsive) {
        width     : 100%;
        text-align: center;
      }
    }

    /* col-right (variation)
     * Page: assessments
     */
    .col-right.assessments {
      h2 {
        font-size    : 1.5em;
        margin-bottom: 0;
      }

      div.container-flex {
        display    : flex;
        align-items: center;
        height     : 90%;

        @media (max-width: $sizeResponsive) {
          display: block;
        }
      }

      li {
        a {
          line-height: 2.5em;
        }

        p {
          margin : 0;
          padding: 0;
          color  : #707070;
        }
      }

      // border Underline
      li::after {
        content      : " ";
        width        : 85%;
        border-bottom: 2px solid #C2C2C2;
        display      : block;

        @media (max-width: $sizeResponsive) {
          width: 100%;
        }
      }

      li:last-child::after {
        border-bottom: 0px;
      }
    }

    /* col-right (variation)
     * Page: overview
     */
    .col-right.overview {
      a {
        color: #00b2ed;
      }

      a.active {
        color: #909090;
      }
    }

  }


  /* ramp_lifechart_page__detail */
  &__detail {
    width        : 70%;
    border       : 2px solid #909090;
    min-height   : 380px;
    border-radius: 10px;

    @media (max-width: $sizeResponsive) {
      width: 100%;
    }

